import { RouterModule, Routes } from '@angular/router';
import { AuthComponent } from './auth.component';
import { HomeComponent } from '../home/home.component';

export const appRoutes: Routes = [{
    path: '', component: AuthComponent, children: [
        { path: 'home', component: HomeComponent },
        { path: 'article', loadChildren: () => import('../article/article.module').then(m => m.ArticleModule) },
        { path: 'devpatterns', loadChildren: () => import('../devpatterns/devpatterns.module').then(m => m.DevpatternsModule) },
        { path: 'technology', loadChildren: () => import('../technology/technology.module').then(m => m.TechnologyModule) },
        { path: 'contact', loadChildren: () => import('../contact/contact.module').then(m => m.ContactModule) },
        { path: 'devopsmachine', loadChildren: () => import('../devpatterns/devopsmachine/devopsmachine.module').then(m => m.DevopsmachineModule) },
        { path: 'gitflow', loadChildren: () => import('../devpatterns/gitflow/gitflow.module').then(m => m.GitflowModule) },
        { path: 'devprocscrum', loadChildren: () => import('../devpatterns/developmentprocessscrum/developmentprocessscrummodule').then(m => m.DevelopmentprocessscrumModule) }
    ]
}];
